import React, { useEffect, useState ,useContext }  from 'react' ;
import './fournisseur.css';
import { useHistory }     from "react-router-dom";
import deco_bar           from '../images/deco_bar_blanc.png';
import footer_fond        from '../images/footer_fond.png';
import axios              from 'axios';
import Pffournisseurs     from './pffournisseurs';
import Footer             from '../1-footer/footer';
import { useParams }      from 'react-router-dom';
import {AuthConte}        from '../../heaauth/autfront'
  
export default function Fournisseur() {

    let history = useHistory() ;

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      });
      
let   {id}  = useParams() ;
const {ico} = useContext(AuthConte) ; 
      
const {stateAuth}                          = useContext(AuthConte) ; 
const [datafournisseur,setdatafournisseur] = useState([]) ;
const [serc,setserc]                       = useState("") ;
const [nom_user,setNom_user]               = useState("") ;
const [avy_ico,setavy_ico]                 = useState(200) ;

var styla1 = {
    marginTop: avy_ico,
  };
  
    var styla3 = {
      marginTop: 80,
    };
  
  useEffect( ()=>{
    if(ico === true){
      setavy_ico(260)
   
    }else{
      setavy_ico(50)
    }
  
  })


useEffect( ()=>{
    axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        setNom_user(response.data.username)
    })

    axios.get(`https://gadana.dev301.com/gmao_version2/fournisseurs/get_fournisseur/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        setdatafournisseur(response.data)
        console.log(response.data)
    })
},[])


const FournisseurGroupe = datafournisseur.filter( (item, index) => {
    if (serc === "") {
        return (
            <Pffournisseurs key={index}
            four_id = {item.id}
            nom     = {item.nom}
            mail    = {item.mail}
            contact = {item.contact}
            adresse  = {item.adresse}
            marche  = {item.marche}
            />
        )
    } else if (item.nom.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
        return (
            <Pffournisseurs key={index}
            four_id = {item.id}
            nom     = {item.nom}
            mail    = {item.mail}
            contact = {item.contact}
            adresse  = {item.adresse}
            marche  = {item.marche}
            />
        )
    }
}).map((item, index) => {
    return (
        <Pffournisseurs key={index}
            four_id = {item.id}
            nom     = {item.nom}
            mail    = {item.mail}
            contact = {item.contact}
            adresse  = {item.adresse}
            marche  = {item.marche}
        />
    )
})

const Affiche = stateAuth.username === nom_user     ? FournisseurGroupe : <div></div>
const height_position              =   window.innerWidth < 650 ? styla1 : styla3

    return (
        <div className='container_accueil_parent_fournisseur' style={height_position}>
            <div className='container_bas_nav'>
                <div className='titre_img_fournisseur'>
                    <div className='titre_F'>
                        <span >fournisseurs</span>
                    </div>
                    <img
                        src={deco_bar}
                        alt="new"
                        className="deco_bar_contact"
                    />
                </div>

                <div className="container_recherche">
                    <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder='Nom' onChange={(e) => { setserc(e.target.value) }} />
                    <label for="name" className="recherche_name" >
                        <span className="recontent-span" ><i className="fas fa-search"></i></span>
                    </label>
                </div>
            </div>
            <div className='container_fournisseurs'>
                {Affiche}
            </div>
            <div className='container_fournisseur_footer'>
                <Footer />
            </div>
            <img
                src={footer_fond}
                alt="new"
                className="footer_image_fond_fournisseurs"
            />
        </div>
    )
}
