import React,{useContext,useState} from 'react';
import './feuille_inscription.css';
import axios          from 'axios';
import { Link }       from 'react-router-dom';
import { useHistory } from 'react-router';
import { AuthConte }  from '../../../heaauth/autfront';

export default function Inc_logine() {

let history = useHistory() ;

const data ={
    username  :'',
    password  :'' 
}

    const {setstateAuth}              = useContext(AuthConte)
    const [info,setInfo]              = useState(data) ;
    const [alerterror,setAlerteerror] = useState('')

    const handlechanchenge=(e)=>{
        setInfo({...info, [e.target.id]:e.target.value })
    }
    
    const LOGIN_page=()=>{
      const {username,password} = info 
      axios.post("https://gadana.dev301.com/gmao_version2/users/log",{
            username        :username,
            password        :password
      }).then( (response)=>{

        if (response.data.error) {
            setAlerteerror(response.data.error);
          } else {
            localStorage.setItem('accessToken',response.data.token)
            console.log(response.data)
            setstateAuth({username:response.data.username,id:response.data.id,status:true})
                 history.push(`/accueil/${response.data.id}`)
               
       }})  
    }




  return (
      <>
          <div className='container_inc'>
              <div className='containe_second_img_login'>
             
                  <div className='container_logine_input'>
                  <div className='titre_feuille_l'>
                      <span>Login</span>
                  </div>
                      <div className='container_input_text_logine'>
                          <div className='logine_pass_oublier'>
                              <span>{alerterror}</span>
                          </div>
                          <div className='ligne_inc'>
                              <div className='gauche_ligne'>
                                  <span>Nom </span>
                                  <span> : </span>
                              </div>
                              <div className="container_recherche_inc">
                                  <input type="text" name="name" autoComplete="off" required type="text" className="container_input_inc" placeholder='Nom' id="username" onChange={handlechanchenge} />
                                  <label for="name" className="recherche_name_inc" >
                                      <span className="recontent-span_inc" ><i className="fas fas fa-user-tie"></i> </span>
                                  </label>
                              </div>
                          </div>
                          <div className='ligne_inc'>
                              <div className='gauche_ligne'>
                                  <span>Mot de Passe </span>
                                  <span> : </span>
                              </div>
                              <div className="container_recherche_inc">
                                  <input type="text" name="name" autoComplete="off"  type="password" required className="container_input_inc" placeholder='Mot de passe' id="password" onChange={handlechanchenge} />
                                  <label for="name" className="recherche_name_inc" >
                                      <span className="recontent-span_inc" > <i className="fas fa-lock"></i> </span>
                                  </label>
                              </div>
                          </div>
                      </div>
                  </div>

                  <div className='btn_envoyer_inc' onClick={LOGIN_page}>
                      <span className='btn_text_envoyer_inc'>Sign In</span>
                      <span><i className="far fa-paper-plane"></i></span>
                  </div>

                  <div className='logine_pass_oublier'>
                      <span> oublier ?</span>
                  </div>
                  <Link className='logine_pass_oublier' to='/registre_gmao'>
                      <span>Inscription</span>
                  </Link>
              </div>
          </div>
      </>
  );
}
