
import React ,{useState} from 'react';
import'./inventaire.css';
import axios                from 'axios';

export default function Pfcommande({
    inv_id        ='',
    piece         ='',
    reference     ='',
    quantite          ='',
    fournisseur   ='',
    commentaire    ='',
}) {

  const  [quant,setquant]     = useState("");
  const  [comment,setcomment] = useState("");
  const  [vurtuel_quant,setvurtuel_quant]             = useState(quantite);
  const  [vurtuel_commentaire,setvurtuel_commentaire] = useState(commentaire);

  
const Quantite_update_quantite =()=>{
  axios.put("https://gadana.dev301.com/gmao_version2/Inventaires/put_inventaire_quantite",{
    newquantite : quant,
    inv_id      : inv_id,

  },{
        headers: {
          accessToken: localStorage.getItem("accessToken"),
        },
  }
  
  ).then( (response)=>{
      setquant("") ;
      setvurtuel_quant(quant)
     
  })
}

const Quantite_update_comment =()=>{
  axios.put("https://gadana.dev301.com/gmao_version2/Inventaires/put_inventaire_comment",{
    newcomment  : comment,
    inv_id      : inv_id,

  },{
        headers: {
          accessToken: localStorage.getItem("accessToken"),
        },
  }
  
  ).then( (response)=>{
      setcomment("")
      setvurtuel_commentaire(comment)
     
  })
}

  const deletePost = (inv_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/Inventaires/del_inventaire/${inv_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime");
       window.location.reload();
      });
  };
  return (
    <>
 
      <div className='inveantaire2_container'>
        <div className='inventaire2_apres_container'>
          <div className='inventaire2_l1'>
              <span >{piece}</span>        
          </div>

          <div className='inventaire2_quantite_container'>
            <div className='inventaire2_nb_quantite'>
              <span >{vurtuel_quant}</span>
            </div>
            <div >
              <input className='inventaire2_input_quantite'id ="quant" value={quant} onChange={(e) => { setquant(e.target.value) }} />
            </div>
         
              <div className='inventaire2_btn_mettre_a_jour'onClick={Quantite_update_quantite}>
                <span >Mettre a jour</span>
              </div>
          
          </div>

          <div className='inventaire2_le_ref_cont'>
            <span className='inveantaire2_nb_ref'>{reference}</span>
          </div>
          <div className='inventaire2_a1'>          
              <span >{fournisseur}</span>
          </div>
       
          <div className='inventaire2_le_comment'>
            <div className='div_vutuel_inventaire'>
              <span >{vurtuel_commentaire}</span>
            </div>

            <div className="inventaire2_container_cherche">
              <input type="text" name="name" autoComplete="off" required type="text" className="iventaire2_input_text" placeholder='commentaire' id ="comment" value={comment} placeholder='Commentaire' onChange={(e) => { setcomment(e.target.value) }} />
              <label for="name" className="inventaire2_le_label" >
                <span className="inventaire2_icon_i" ><i className="fas fa-comments"></i></span>
              </label>
            </div>
            <div className='inventaire2_btn_mettre_a_jour2' onClick={Quantite_update_comment}>
              <span >Mettre a jour</span>
            </div>
          </div>
          <div className='inventaire2_icon_de_delete'  onClick={()=>deletePost(inv_id)}>
            <span> <i className="fas fa-trash"></i></span>
          </div>
        </div>
      </div>
    </>
  );
}


