import React, { useState }  from 'react' ;
import axios                from 'axios';

export default function Pffournisseurs(
    {       four_id  = '',
            nom      ='',
            mail     ='',
            contact  ='',
            adresse   ="",
            marche   ='',
    }
)
 {

const [plier_box,setplier_box]=useState(false) ;

const Rmiov=()=>{
    setplier_box(!plier_box)
}

const deletePost = (four_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/fournisseurs/del_fournisseur/${four_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime")
       window.location.reload();
      });
  };

const className_box = plier_box ? "container_box_parent_fournisseur" : "container_box_parent_fournisseur_click"
const Ladaga        = plier_box ? 
<>
        <div className='container_bas_feuille'>
            <div className="box_container_fournisseur">

                <div className="box_lign1_fournisseur">
                    <div className='text_icon_fournisseur'>
                        <p > Mail</p>
                        <i className="far fa-envelope-open"> </i>
                    </div>
                    <p className='point_double_fournisseur'>:</p>
                </div>
                <div className="result_1_fournisseur">
                    <p className='mail_p'>{mail}</p>
                </div>
            </div>

            <div className="box_container_fournisseur">
                <div className="box_lign1_fournisseur">
                    <div className='text_icon_fournisseur'>
                        <p >Contact </p>
                        <i className="far fa-address-card"></i>
                    </div>
                    <p className='point_double_fournisseur'>:</p>
                </div>
                <div className="result_2_fournisseur">
                    <p>{contact}</p>
                </div>
            </div>

            <div className="box_container_fournisseur">
                <div className="box_lign1_fournisseur">
                    <div className='text_icon_fournisseur'>
                        <p > Adresse</p>
                        <i className="fas fa-map-marker"> </i>
                    </div>

                    <p className='point_double_fournisseur'>:</p>
                </div>
                <div className="result_1_fournisseur">
                    <p className='mail_p'>{adresse}</p>
                </div>
            </div>

            <div className="box_container_fournisseur">
                <div className="box_lign1_fournisseur">
                    <div className='text_icon_fournisseur'>
                        <p >March </p>
                        <i className="fas fa-dolly"></i>
                    </div>
                    <p className='point_double_fournisseur'>:</p>
                </div>
                <div className="result_2_fournisseur">
                    <p> {marche}</p>
                </div>

            </div>
        </div>
        <div className='contact_supre' onClick={()=>deletePost(four_id)}>
            <i className="fas fa-trash"></i>
        </div>
    </> 
:
(
   <div></div>
)

    return (
        <>
            <div className={className_box}>
                <div className="box_qui_glisse_fournisseur" onClick={Rmiov} >
                    <span className="spa">{nom}</span>
                    <span className="spa"><i className="far fa-address-card"></i> {contact}</span>
                </div>
                <div>
                    {Ladaga}
                </div>
            </div>
        </>
    )
}
