import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import       axios   from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';

export default function Insc_contact() {

    let {id} = useParams() ;
  /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/

    const {stateAuth} = useContext(AuthConte) ; 

    const data = {
        service : "",
        nom     : "",
        mail    : "",
        contact : "",
    }

    const Envoi_Contact = () => {
        const {  mail, contact, service, nom } = info;
        axios.post('https://gadana.dev301.com/gmao_version2/Contacts/post_contact', {
            service      : service,
            nom          : nom,
            mail         : mail,
            contact      : contact,
            username     : stateAuth.username ,
            UserId       : id     
        },
        {
            headers: {
              accessToken: localStorage.getItem("accessToken"),
            },
          }
        ).then((response) => {
            console.log(" bien envoyer ")
            setinfo({ ...data })
        })
    }
    const [info, setinfo]                = useState(data)
    
    const handleChange = e => {
        setinfo({ ...info, [e.target.id]: e.target.value });
    }


    const { service, nom, mail, contact} = info;
    return (
        <div className='container_inc'>
            <div className='containe_second_img_contact'>
               
                <div className='container_input_text'>
                <div className='titre_feuille'>
                    <span>Contact</span>
                </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Service </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='service' maxLength={40} type="text" id="service"  onChange={handleChange} value={service}  name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-house-user"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Nom </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='nom' maxLength={30} type="text" id="nom"  onChange={handleChange} value={nom}  name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fas fa-user-tie"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Mail </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='mail' maxLength={50} type="text"  id="mail"  onChange={handleChange} value={mail}   name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-envelope-open"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Contact </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='contact' maxLength={24} type="text"  id="contact"  onChange={handleChange} value={contact}   name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-address-card"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Contact}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
