import React, { useState ,useEffect,useContext} from 'react';
import './inscription_accueil.css';
import Insc_commande       from './feuille_inscription/insc_commande';
import Insc_contact        from './feuille_inscription/insc_contact';
import Insc_fournisseur    from './feuille_inscription/insc_fournisseur';
import Insc_inventaire     from './feuille_inscription/insc_inventaire';
import Insc_maintenance    from './feuille_inscription/insc_maintenance';
import Insc_procedure      from './feuille_inscription/insc_procedure';
import Insc_Note           from './feuille_inscription/insc_note';
import Inc_archive         from './feuille_inscription/insc_archive'

import { AuthConte}        from '../../heaauth/autfront'

export default function Inscription_accueil() {


    const [r1,setr1] = useState(false)
    const [r2,setr2] = useState(false)
    const [r3,setr3] = useState(true)
    const [r4,setr4] = useState(false)
    
    const [r5,setr5] = useState(false)
    const [r6,setr6] = useState(false)
    const [r7,setr7] = useState(false)
    const [r8,setr8] = useState(false)

    const Affiche_fiche_inscription1 =()=>{
        setr1(true)
        setr2(false)
        setr3(false)
        setr4(false)
        setr5(false)
        setr6(false)
        setr7(false)
        setr8(false)
    }

    const Affiche_fiche_inscription2 =()=>{
        setr1(false)
        setr2(true)
        setr3(false)
        setr4(false)
        setr5(false)
        setr6(false)
        setr7(false)
        setr8(false)
    }
    const Affiche_fiche_inscription3 =()=>{
        setr1(false)
        setr2(false)
        setr3(true)
        setr4(false)
        setr5(false)
        setr6(false)
        setr7(false)
        setr8(false)
    }
    const Affiche_fiche_inscription4 =()=>{
        setr1(false)
        setr2(false)
        setr3(false)
        setr4(true)
        setr5(false)
        setr6(false)
        setr7(false)
        setr8(false)
    }
    const Affiche_fiche_inscription5 =()=>{
        setr1(false)
        setr2(false)
        setr3(false)
        setr4(false)
        setr5(true)
        setr6(false)
        setr7(false)
        setr8(false)
    }

    const Affiche_fiche_inscription6 =()=>{
        setr1(false)
        setr2(false)
        setr3(false)
        setr4(false)
        setr5(false)
        setr6(true)
        setr7(false)
        setr8(false)
    }
    const Affiche_fiche_inscription7 =()=>{
        setr1(false)
        setr2(false)
        setr3(false)
        setr4(false)
        setr5(false)
        setr6(false)
        setr7(true)
        setr8(false)
    }
    const Affiche_fiche_inscription8 =()=>{
        setr1(false)
        setr2(false)
        setr3(false)
        setr4(false)
        setr5(false)
        setr6(false)
        setr7(false)
        setr8(true)
    }

const color1 = r1 ? 'con_titre11' : 'con_titre1'
const color2 = r2 ? 'con_titre11' : 'con_titre1'
const color3 = r3 ? 'con_titre11' : 'con_titre1'
const color4 = r4 ? 'con_titre11' : 'con_titre1'
const color5 = r5 ? 'con_titre22' : 'con_titre2'
const color6 = r6 ? 'con_titre22' : 'con_titre2'
const color7 = r7 ? 'con_titre22' : 'con_titre2'
const color8 = r8 ? 'con_titre22' : 'con_titre2'

const Feuille_insc1 = r1 ? <div><Insc_contact/>     </div> : <div></div>
const Feuille_insc2 = r2 ? <div><Insc_fournisseur /> </div> : <div></div>
const Feuille_insc3 = r3 ? <div><Insc_commande/>    </div> : <div></div>
const Feuille_insc4 = r4 ? <div><Insc_inventaire/>  </div> : <div></div>
const Feuille_insc5 = r5 ? <div><Inc_archive/>       </div> : <div></div>
const Feuille_insc6 = r6 ? <div><Insc_maintenance/> </div> : <div></div>
const Feuille_insc7 = r7 ? <div><Insc_procedure/>   </div> : <div></div>
const Feuille_insc8 = r8 ? <div><Insc_Note/>        </div> : <div></div>

const {ico}       = useContext(AuthConte) ; 
const [avy_ico,setavy_ico]               =useState(200) ;

var styla1 = {
  marginTop: avy_ico,
};

  var styla3 = {
    marginTop: 80,
  };

useEffect( ()=>{
  if(ico === true){
    setavy_ico(200)
 
  }else{
    setavy_ico(50)
  }

})

const height_position     =  window.innerWidth < 650 ? styla1 : styla3
    return (
        <div className='container_accueil_parent_inscripton' style={height_position}>
            <div className='container_bas_nav_inscription'>
         
                    <div className='container_liste1'>
                        <span className={color1} onClick={Affiche_fiche_inscription1}>Contact</span>
                        <span className={color2} onClick={Affiche_fiche_inscription2}>Fournisseur</span>
                        <span className={color3} onClick={Affiche_fiche_inscription3}>commande</span>
                        <span className={color4} onClick={Affiche_fiche_inscription4}>inventaire</span>
                    </div>
                    <div className='container_liste1'>
                        <span className={color5} onClick={Affiche_fiche_inscription5}>Archive</span>
                        <span className={color6} onClick={Affiche_fiche_inscription6}>maintenance</span>
                        <span className={color7} onClick={Affiche_fiche_inscription7}>procedure</span>
                        <span className={color8} onClick={Affiche_fiche_inscription8}>info</span>
                    </div>
               
            </div>
            <div>
                {Feuille_insc1}
                {Feuille_insc2}
                {Feuille_insc3}
                {Feuille_insc4}
                {Feuille_insc5}
                {Feuille_insc6}
                {Feuille_insc7}
                {Feuille_insc8}
            </div>
            <div >
            </div>
        </div>
      
    )
}
