import React, { useState } from 'react';
import'./commande.css';
import axios               from 'axios';

export default function Pfcommande({
    commande_id  ='',
    piece        ='',
    fournisseur  ='',
    quantite     ='',
    reference    ='',
    commentaire  ='',
}) {

const  [quant,setquant]     = useState("");
const  [comment,setcomment] = useState("");
const  [vurtuel_quant,setvurtuel_quant]             = useState(quantite);
const  [vurtuel_commentaire,setvurtuel_commentaire] = useState(commentaire);

const Quantite_update_quantite =()=>{
    axios.put("https://gadana.dev301.com/gmao_version2/Commandes/put_commande_quantite",{
      newquantite : quant,
      idkely      : commande_id,

    },{
          headers: {
            accessToken: localStorage.getItem("accessToken"),
          },
    }
    
    ).then( (response)=>{
        setquant("") ;
        setvurtuel_quant(quant)
       
    })
}

const Quantite_update_comment =()=>{
    axios.put("https://gadana.dev301.com/gmao_version2/Commandes/put_commande_comment",{
      newcomment  : comment,
      idkely      : commande_id,

    },{
          headers: {
            accessToken: localStorage.getItem("accessToken"),
          },
    }
    
    ).then( (response)=>{
        setcomment("")
        setvurtuel_commentaire(comment)
       
    })
}

const deletePost = (commande_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/Commandes/del_commande/${commande_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime");
       window.location.reload();
      
      });
  };

  return (
      <>      
          <div >
              <div className='container_liste1_commande2'>
                  <div className='con_titre1_commande2_nom'>
                      <span >{piece}</span>
                  </div>
                  <div className='con_titre1_commande2_nom' >
                      <span >{fournisseur}</span>
                  </div>
                  <div className='con_titre1_commande2_quantite'>
                      <div className='cmd2_fournissseur'>
                          <span >{vurtuel_quant}</span>
                      </div>
                      <div >
                          <input className='input_quantite' id="quant" value={quant} onChange={(e) => { setquant(e.target.value) }} />
                      </div>

                      <div className='btn_mettre_a_jour' onClick={Quantite_update_quantite}>
                          <span className='btn_mettre_a_jour_text'>Mettre a jour</span>
                      </div>
                  </div>

                  <div className='con_titre1_commande2_nom' >
                      <span >{reference}</span>
                  </div>

                  <div className='con_titre1_commande2_commentaire'>
                      <div className='div_vutuel_commentaire'>
                          <p >{vurtuel_commentaire}</p>
                      </div>
                      <div>
                          <div className='btn_et_comment'>
                              <div className="container_commentaire_input">
                                  <input type="text" name="name" autoComplete="off" required className="commentaire_input" id="comment" value={comment} placeholder='Commentaire' onChange={(e) => { setcomment(e.target.value) }} />

                                  <label for="name" className="label_input" >
                                      <span className="icon_commentaire_input" ><i className="fas fa-comments"></i></span>
                                  </label>
                              </div>
                              <div className='btn_mettre_a_jour2' onClick={Quantite_update_comment}>
                                  <span >Mettre a jour</span>
                              </div>
                          </div>
                      </div>
                  </div>
                  <span className='con_titre1_commande2_date'>23/01/2022</span>
                  <div className='con_titre1_commande2_delete' onClick={() => deletePost(commande_id)}>
                      <span> <i className="fas fa-trash"></i></span>
                  </div>
              </div>
          </div>
      </>
  );
}


