import React from 'react';
import'./nacre.css';

export default function Pfnacre({
    Codes     ='',
    Intitules ='',
    repere    = '',
    Nature
}) {
  return (
          <div className='container_bas_nav_nacre2'>
              <div className='container_liste1_nacre2'>
                  <span className='con_titre1_nacre2_code'>{Codes}</span>
                 <div className='con_titre1_nacre2_Intitules'>
                 <span className='text_intitule' >{Intitules}</span>
                 </div>
                  <span className='con_titre1_nacre2'>{Nature}</span>
                  <span className='con_titre1_nacre2_code'>{repere}</span>
              </div>
          </div>
  );
}
