import React, { useEffect, useState ,useContext }  from 'react' ;
import  {AuthConte}   from '../../heaauth/autfront';
import { useHistory ,useParams }     from "react-router-dom";
import '../8-procedure/procedure.css'
import axios               from 'axios';
import Pfnote              from './pfnote';
import deco_bar            from '../images/deco_bar_blanc.png';


export default function Note() {

    let history = useHistory() ;
    let {id} = useParams() ;
    
    const {stateAuth}                = useContext(AuthConte) ; 
    const [titre_user,settitre_user] = useState("")

    const [data_note,setdata_note]   = useState([]) ;
    const [serc,setserc]             = useState("") ;
    const {ico}       = useContext(AuthConte) ; 
    const [avy_ico,setavy_ico]               =useState(200) ;

var styla1 = {
  marginTop: avy_ico,
};

  var styla3 = {
    marginTop: 80,
  };

useEffect( ()=>{
  if(ico === true){
    setavy_ico(210)
 
  }else{
    setavy_ico(50)
  }

})


useEffect(() => {
    if (!localStorage.getItem("accessToken")) {
      history.push("/");
    }
  }, []);

useEffect( ()=>{

    axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        settitre_user(response.data.username)
    })

    axios.get(`https://gadana.dev301.com/gmao_version2/Notes/get_note/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        setdata_note(response.data)
    })
},[])


    const Liste_de_code_info = data_note.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfnote
                   note_id ={item.id}
                   titre={item.titre}
                   info1={item.info1}
                   info2={item.info2}
                   info3={item.info3}
                    />
                </div>
            )
        } else if (item.titre.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfnote
                       note_id ={item.id}
                       titre={item.titre}
                       info1={item.info1}
                       info2={item.info2}
                       info3={item.info3}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfnote
                   note_id ={item.id}
                   titre={item.titre}
                   info1={item.info1}
                   info2={item.info2}
                   info3={item.info3}
                />
            </div>
        )
    })

    const height_position     =  window.innerWidth < 650 ? styla1 : styla3  
     const Affiche = stateAuth.username === titre_user ? Liste_de_code_info : <div></div>

  return (
<div className='procedure_container_nacre_parent_inscripton'  style={height_position}>

<div className='container_bas_procedure'>
    <div className='titre_img_procedure'>
        <div className='titre_C'>
            <span >Note</span>
        </div>
        <img
            src={deco_bar}
            alt="new"
            className="deco_bar_procedure"
        />
    </div>
        <div className="container_recherche">
            <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder="Titre" onChange={(e) => { setserc(e.target.value) }} />
            <label for="name" className="recherche_name" >
                <span className="recontent-span" ><i className="fas fa-search"></i></span>
            </label>
    </div>
</div>
<div className='procedure_pf_container' >
    {Affiche}
</div>
</div>
  )
  ;
}
