
import React from 'react';


export default function Pfdevis2({
    piece ='',
    quantite         ='',
    reference        ='',
    commentaire   ='',
}) {
  return (
    <div className='impr2_cont'>
        <div className='impr2_text_nom'>
                <span >{piece}</span>
        </div>

        <div className='impr2_text' >
        <span >{quantite}</span>

        </div>

        <div className='impr2_text'>
            <span >{reference}</span>
        </div>
        <div className='impr2_text'>
            <span >{reference}</span>
        </div>
        <div className='impr2_text_comment'>
              <span>{commentaire}</span> 
        </div>
</div>
  );
}

