import React, { useEffect, useState ,useContext }  from 'react' ;
import './contact.css' ; 
import { useHistory, useParams }   from 'react-router-dom';
import  {AuthConte}   from '../../heaauth/autfront';
import footer_fond    from '../images/footer_fond.png';
import deco_bar       from '../images/deco_bar_blanc.png';
import axios          from 'axios';
import Pfcontact      from './pfcontact';
import Footer         from '../1-footer/footer';

export default function Contact() {

    let history = useHistory() ;
    
    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, [])
    
    const {ico}       = useContext(AuthConte) ; 
    const [data_contact,setData_contact] = useState([]);
    const [serc,setserc]                 = useState("") ;
    const [avy_ico,setavy_ico]                 = useState(200) ;

var styla1 = {
    marginTop: avy_ico,
  };
  
    var styla3 = {
      marginTop: 80,
    };
  
  useEffect( ()=>{
    if(ico === true){
      setavy_ico(250)
   
    }else{
      setavy_ico(50)
    }
  
  })

    let {id} = useParams() ;
    
    const {stateAuth} = useContext(AuthConte) ; 
    const [nom_user,setNom_user]               = useState("")

useEffect( ()=>{
    axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        setNom_user(response.data.username)
    })

    axios.get(`https://gadana.dev301.com/gmao_version2/Contacts/get_contact/${id}`,{
        headers:{accessToken: localStorage.getItem("accessToken")},
    })
    .then( (response)=>{
        setData_contact(response.data)
    
    })
},[])


const Contact_Groupe = data_contact.filter( (item, index) => {
    if (serc === "") {
        return (
            <Pfcontact key={index}
            cont_id = {item.id}
            service = {item.service}
            nom     = {item.nom}
            mail    = {item.mail}
            contact  = {item.contact}
            />
        )
    } else if (item.service.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
        return (
            <Pfcontact key={index}
            cont_id = {item.id}
            service = {item.service}
            nom     = {item.nom}
            mail    = {item.mail}
            contact  = {item.contact}
            />
        )
    }
}).map((item, index) => {
    return (
        <Pfcontact key={index}
        cont_id = {item.id}
        service = {item.service}
        nom     = {item.nom}
        mail    = {item.mail}
        contact  = {item.contact}
        />
    )
})

const height_position              =    window.innerWidth < 650 ? styla1 : styla3
const Affiche = stateAuth.username === nom_user ? Contact_Groupe         : <div></div>

    return (
        <div className='container_accueil_parent_contact' style={height_position}>
            <div className='container_bas_nav_contact'>
                <div className='titre_img_contact'>
                    <div className='titre_C'>
                        <span >Contact</span>
                    </div>
                    <img
                        src={deco_bar}
                        alt="new"
                        className="deco_bar_contact"
                    />
                </div>
                <div className="container_recherche">
                    <input type="text" name="name" autoComplete="off" required type="text" className="container_input" onChange={(e) => { setserc(e.target.value) }} placeholder='Service' />
                    <label for="name" className="recherche_name" >
                        <span className="recontent-span" ><i className="fas fa-search"></i></span>
                    </label>
                </div>
            </div>
            <div className='container_contact'>
                {Affiche}
            </div>

            <div className="container_contact_footer" >
                <Footer />
            </div>
            <img
                src={footer_fond}
                alt="new"
                className="footer_image_fond_contact"
            />
        </div>
    )
}
