import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import    axios      from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';

export default function Insc_devis() {
    let {id} = useParams() ;
  /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/

    const {stateAuth} = useContext(AuthConte) ; 

    const data = {
        piece        : "",
        fournisseur  : "",
        reference    : "",
        quantite     : "",
        commentaire  : ""
    }

    const Envoi_Devis= () => {
        const {  reference, quantite, piece, fournisseur , commentaire} = info;
        axios.post("https://gadana.dev301.com/gmao_version2/Devis/post_devi", {
            piece        : piece,
            fournisseur  : fournisseur,
            reference    : reference,
            quantite     : quantite,
            commentaire  : commentaire ,
            username     : stateAuth.username ,
            UserId       : id ,
        },
        {
            headers: {
              accessToken: localStorage.getItem("accessToken"),
            },
          }
        ).then((response) => {
            console.log(" bien envoyer ")
            setinfo({ ...data })
            window.location.reload();
        })
    }
    const [info, setinfo]                = useState(data)
    
    const handleChange = e => {
        setinfo({ ...info, [e.target.id]: e.target.value });
    }

    const { piece, fournisseur, reference, quantite,commentaire} = info;

    return (
        <div className='container_inc'>
            <div className='containe_second_img_devis2'>
            <div className='container_input_text2'>
                    <div className='titre_feuille'>
                        <span></span>
                    </div>    
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Piece </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='piece' type="text" name="name" id="piece"  onChange={handleChange} value={piece} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-toolbox"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Fournisseur </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='fournisseur' type="text" name="name"  id="fournisseur"  onChange={handleChange} value={fournisseur} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-store"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Reference </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='reference' type="text" name="name" id="reference"  onChange={handleChange} value={reference} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-amount-down"></i></span>
                            </label>
                        </div>

                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Quantit </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='quantit' maxLength={6} type="text" name="name" id="quantite"  onChange={handleChange} value={quantite} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-store"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Caracteristique </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='caracteristique' type="text" name="name" id="commentaire"  onChange={handleChange} value={commentaire} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-comments"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Devis}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
