import React, { useState }  from 'react' ;
import axios                from 'axios';

export default function Pfcontact({
    cont_id ='',
    service = '',
    nom     = '',
    mail    = '',
    contact  =''
}) {

const [plier_box,setplier_box]=useState(false)
const Rmiov=()=>{
    setplier_box(!plier_box)
}

const deletePost = (four_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/Contacts/del_contact/${four_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime")
       window.location.reload();
      });
  };
  
const className_box = plier_box ? "container_box_parent_contact" : "container_box_parent_contact_click"
const Func_box_plie = plier_box ? 
<>
        <div className='contact_container_bas_feuille'>
            <div className="box_container_contact">
                <div className="box_lign1_contact">
                    <div className='text_icon_contact'>
                        <p > Service</p>
                        <i className="fas fa-house-user"> </i>
                    </div>
                    <p className='point_double_contact'>:</p>
                </div>
                <div className="result_1_contact">
                    <p className='mail_p'>{service}</p>
                </div>
            </div>

            <div className="box_container_contact">
                <div className="box_lign1_contact">
                    <div className='text_icon_contact'>
                        <p > Nom</p>
                        <i className="fas fa-user-tie"> </i>
                    </div>
                    <p className='point_double_contact'>:</p>
                </div>
                <div className="result_1_contact">
                    <p className='mail_p'>{nom}</p>
                </div>
            </div>

            <div className="box_container_contact">
                <div className="box_lign1_contact">
                    <div className='text_icon_contact'>
                        <p > Mail</p>
                        <i className="far fa-envelope-open"> </i>
                    </div>
                    <p className='point_double_contact'>:</p>
                </div>
                <div className="result_1_contact">
                    <p className='mail_p'>{mail}</p>
                </div>
            </div>

            <div className="box_container_contact">
                <div className="box_lign1_contact">
                    <div className='text_icon_contact'>
                        <p > Contact</p>
                        <i className="far fa-address-card"> </i>
                    </div>
                    <p className='point_double_contact'>:</p>
                </div>
                <div className="result_1_contact">
                    <p className='mail_p'>{contact}</p>
                </div>
            </div>
        </div>
        <div className='fourni_supre'onClick={()=>deletePost(cont_id)}>
            <i className="fas fa-trash"></i>
        </div>
</>
:
<div></div>
    return (
        <>
            <div className={className_box} >
                <div className="box_qui_glisse_contact" onClick={Rmiov}>
                    <span className="spa">{nom}</span>
                    <span className="spa"><i className="far fa-address-card"></i> {contact}</span>
                </div>

                <div>
                    {Func_box_plie}
                </div>
            </div>
        </>
      
    )
}
