import React, { useEffect, useState ,useContext }  from 'react' ;
import  {AuthConte}   from '../../heaauth/autfront';
import { useHistory ,useParams }     from "react-router-dom";
import'./maintenance.css';
import Pfmaintenance  from './pfmaintenance';
import axios          from 'axios';
import deco_bar       from '../images/deco_bar_blanc.png';

export default function Maintenance() {
    let history = useHistory() ;
    let {id} = useParams() ;
    
    const {stateAuth} = useContext(AuthConte) ; 
const [nom_user,setNom_user]               = useState("")

    const [serc,setserc]                         = useState("") ;
    const [choix,setChoix]                       = useState(false) ;
    const [data_maintenance,setData_maintenance] = useState([]);
    
    const {ico}       = useContext(AuthConte) ; 
    const [avy_ico,setavy_ico]               =useState(200) ;

var styla1 = {
  marginTop: avy_ico,
};

  var styla3 = {
    marginTop: 160,
  };

useEffect( ()=>{
  if(ico === true){
    setavy_ico(315)
 
  }else{
    setavy_ico(100)
  }

})

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, []);

      useEffect( ()=>{

        axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
            headers:{accessToken: localStorage.getItem("accessToken")},
        })
        .then( (response)=>{
            setNom_user(response.data.username)
        })
    
        axios.get(`https://gadana.dev301.com/gmao_version2/Maintenances/get_maintenance/${id}`,{
            headers:{accessToken: localStorage.getItem("accessToken")},
        })
        .then( (response)=>{
            setData_maintenance(response.data)
        })
    },[])
    
    
    const Liste_de_code_nacre = data_maintenance.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfmaintenance
                        maint_id    = {item.id}
                        salle       = {item.salle}
                        materiel    = {item.materiel}
                        panne       = {item.panne}
                        commentaire = {item.commentaire}
                    />
                </div>
            )
        } else if (item.salle.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfmaintenance
                                   maint_id    = {item.id}
                                   salle       = {item.salle}
                                   materiel    = {item.materiel}
                                   panne       = {item.panne}
                                   commentaire = {item.commentaire}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfmaintenance
                            maint_id    = {item.id}
                            salle       = {item.salle}
                            materiel    = {item.materiel}
                            panne       = {item.panne}
                            commentaire = {item.commentaire}
                />
            </div>
        )
    })

    const Liste_de_code_materiel = data_maintenance.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfmaintenance
                                   maint_id    = {item.id}
                                   salle       = {item.salle}
                                   materiel    = {item.materiel}
                                   panne       = {item.panne}
                                   commentaire = {item.commentaire}
                    />
                </div>
            )
        } else if (item.materiel.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfmaintenance
                                   maint_id    = {item.id}
                                   salle       = {item.salle}
                                   materiel    = {item.materiel}
                                   panne       = {item.panne}
                                   commentaire = {item.commentaire}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfmaintenance
                        maint_id    = {item.id}
                        salle       = {item.salle}
                        materiel    = {item.materiel}
                        panne       = {item.panne}
                        commentaire = {item.commentaire}
                />
            </div>
        )
    })


    const Btn_choix =()=>{
        setChoix(!choix)
    }

      

    const height_position     =  window.innerWidth < 650 ? styla1 : styla3
    const Nom_choix_placeholder    = choix ?   'Salle'       :   'Machine' 
    const Nom_choix                = choix ?   'Machine'     :   'Salle'
 
    const Icon_choix               = choix ?   'fas fa-align-center'  :   'fas fa-list-ol' 
    const Choix_salle_ou_materiel = choix ? Liste_de_code_nacre : Liste_de_code_materiel
  
    const Affiche = stateAuth.username === nom_user ? Choix_salle_ou_materiel : <div></div>

  return (
      <div className='container_maintenance_parent_inscripton' style={height_position}>
          <div className='container_white_bar'>
              <div className='titre_img_maintenance'>
                  <div className='titre_maintenance'>
                      <span >maintenance</span>
                  </div>
                  <img
                      src={deco_bar}
                      alt="new"
                      className="deco_bar_maintenance"
                  />
              </div>
          </div>
          <div className='cherche_maintenance'>
              <div className='btn_cherche_maintenance' onClick={Btn_choix}>
                  <span className='btn_text_btn_maintenance'>{Nom_choix}</span>
                  <span><i className={Icon_choix}></i></span>
              </div>
              <div className="container_recherche">
                  <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder={Nom_choix_placeholder} onChange={(e) => { setserc(e.target.value) }} />
                  <label for="name" className="recherche_name" >
                      <span className="recontent-span" ><i className="fas fa-search"></i></span>
                  </label>
              </div>
          </div>

          <div className='container_bas_nav_maintenance'>
              <div className='container_liste1_maintenance'>
                  <span className='con_titre1_maintenance_salle'>salle</span>
                  <span className='con_titre1_maintenance_nom_machines'>nom_machine</span>
                  <span className='con_titre1_maintenance'>panne</span>
                  <span className='con_titre1_maintenance_commentaire'>commentaire</span>
                  <span className='con_titre1_maintenance_salle'>Date</span>
                  <span className='con_titre1_maintenance_delete' >delete</span>
               
              </div>
          </div>

          <div >
              {Affiche}
          </div>
      </div>
  )
  ;
}
