import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import       axios   from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';


export default function Insc_maintenance() {
    
    let {id} = useParams() ;
    /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/
  
const {stateAuth} = useContext(AuthConte) ; 
  
      const data = {
          salle  : "",
          materiel  : "",
          panne  : "",
          commentaire  : "",
      }
  
      const Envoi_Maintenance = () => {
          const {   salle, materiel ,panne, commentaire} = info;
          axios.post('https://gadana.dev301.com/gmao_version2/Maintenances/post_maintenance', {
              salle        : salle,
              materiel     : materiel,
              panne        : panne,
              commentaire  : commentaire,
              username     : stateAuth.username ,
              UserId       : id ,
          },
          {
              headers: {
                accessToken: localStorage.getItem("accessToken"),
              },
            }
          ).then((response) => {
              console.log(" bien envoyer ")
              setinfo({ ...data })
          })
      }
      const [info, setinfo]                = useState(data)
      
      const handleChange = e => {
          setinfo({ ...info, [e.target.id]: e.target.value });
      }
  
      const { salle, materiel, panne, commentaire} = info;
    
    return (
        <div className='container_inc'>
            <div className='containe_second_img_maintenance'>
                <div className='container_input_text'>
                <div className='titre_feuille'>
                        <span>Maintenance</span>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Salle </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='salle' type="text" name="name" id="salle"  onChange={handleChange} value={salle} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-laptop-house"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Matriel </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='materiel' type="text" id="materiel"  onChange={handleChange} value={materiel} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-toolbox"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Panne </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='panne' type="text" name="name" id="panne"  onChange={handleChange} value={panne} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-wrench"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Commentaire </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='commentaire' type="text" name="name" id="commentaire"  onChange={handleChange} value={commentaire} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-comments"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Maintenance}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
