
import React, { useEffect, useState } from 'react';
import './premier.css';
import { BrowserRouter as Router, Route, Switch, Link} from "react-router-dom";
import axios               from 'axios';
import { AuthConte }       from '../../heaauth/autfront';
import nav_fond2           from '../images/nav_fond3.png';
import nav_fond3           from '../images/nav_fond3_p.png';
import logo_p              from '../images/soratra2.png';

import Accueil             from '../2-accueil/accueil';
import Fournisseur         from '../3-fournisseurs/fournisseur';
import Contact             from '../4-contact/contact';
import Inscription_accueil from '../7-inscrition_accueil/inscription_accueil';
import Nacre               from '../9-nacre/nacre';
import Maintenance         from '../10-maintenance/maintenance';
import Dans_le_marche      from '../14-dans_le_marche/dans_le_marche';
import Commande            from '../5-commande/commande';
import Inventaire          from '../6-inventaire/inventaire';
import Devis               from '../11-devis/devis';
import Procedure           from '../8-procedure/procedure';
import Note                from '../13-note/note';
import Logine_gmao         from '../15-logine_gmao/logine_gmao';
import Registre_gmao       from '../16-registre_gmao/registre_gmao';


export default function Premier() {

    const [ico,setico]             = useState(false);
    const [ico_fond,setico_fond]   = useState(false);
    const [stateAuth,setstateAuth] = useState({username :"",id:0,status:false}) ;

    
    useEffect( ()=>{
        axios.get("https://gadana.dev301.com/gmao_version2/users/auth",{
            headers:{accessToken:localStorage.getItem("accessToken")}
        })
        .then( (response)=>{
            if(response.data.error){
                setstateAuth({username:'',id:0,status:false})
            }else{
                setstateAuth({
              username:response.data.username,
              id      : response.data.id ,
              status  :true        

          })}
        })
    })

   const id =  stateAuth.id

    const LougOut=()=>{   
        localStorage.removeItem("accessToken")
        setstateAuth({...stateAuth,status:false})  
    }

    const Bar_menu =()=>{
        setico(!ico)
    }

    useEffect( ()=>{
        if(window.innerWidth > 650){
            setico_fond(true)
            setico(true) ;           
        }else{
            setico_fond(false)
        }
        
    })

/*/ NAV  AVANT CONNECTION /*/
const Nav_avant_connection = 
        <div className="listenav3">
            <div>
                <Link className="nav_inc1"     to='/'>Presentation</Link>
            </div>
            <div className='nav_inc_p' >
                <div>
                    <Link className="nav_inc2" to='/registre_gmao' >Inscription</Link>
                </div>
                <div className='nac_inc_p3' >
                    <Link className="nav_inc3" to='/'>Login</Link>
                    <span><i class="fas fa-sign-out-alt"></i></span>
                </div>
            </div>
        </div>

/*/ NAV QUANS C 'EST CONECTE  /*/
    const  Nav_connecte = 
            <div className="nav_connecte">
                <div className="nav_connecte_2">
                    <Link className="nav_connecte_3" to={`/accueil/${id}`}   >  Accueil </Link>
                    <Link className="nav_connecte_3" to={`/contact/${id}`}   >  Contact </Link>
                    <Link className="nav_connecte_3" to={`/inventaire/${id}`}>Inventaire</Link>
                </div>
                <div className="btn_loug_out_container">
        
                    <div onClick={LougOut}  >
                        <Link to='/' className="btn_log_out">
                            Logout
                            <i class="fas fa-sign-out-alt"  ></i>
                        </Link>
                    </div>
                </div>
            </div>
      
    
    const Choix_nav_connection     = !stateAuth.status ? Nav_avant_connection      :  Nav_connecte 
    const Nav_menu_ecran_pour_tel  = ico               ? Choix_nav_connection      : <div></div>
    const fond_premier             = ico_fond          ? nav_fond2                 : nav_fond3
     return (
        <div className='container_premier'>
            <AuthConte.Provider  value={{stateAuth,setstateAuth , ico}}>
                <Router>
                    <div className="contanav1">
                        <div className='menu_logo_pet'>
                            <div className='ml1' onClick={Bar_menu}>
                                <i className="fas fa-bars"></i>
                            </div>
                            <div>
                            <img
                                src={logo_p}
                                alt="new"
                                className="logo_p"
                            />
                            </div>
                          
                        </div>
                        {Nav_menu_ecran_pour_tel}
                        <div >
                            <img
                                src={fond_premier}
                                alt="new"
                                className="nav_image_fond"
                            />
                        </div>
                    </div>

                    <div>
                        <Switch>
                            <Route path='/'                           exact component    = {Logine_gmao}/>  
                            <Route path='/registre_gmao'              exact component    = {Registre_gmao}/>

                            <Route path='/dans_le_marche/:id'         exact component    = {Dans_le_marche}/>
                            <Route path='/commande/:id'               exact component    = {Commande}/>
                            <Route path='/accueil/:id'                exact component    = {Accueil}/>  
                            <Route path='/fournisseur/:id'            exact component    = {Fournisseur}/>
                            <Route path='/maintenance/:id'            exact component    = {Maintenance}/>
                            <Route path='/nacre/:id'                  exact component    = {Nacre}/>
                            <Route path='/contact/:id'                exact component    = {Contact}/>
                            <Route path='/inscription_accueil/:id'    exact component    = {Inscription_accueil}/> 
                            <Route path='/devis/:id'                  exact component    = {Devis}/>
                            <Route path='/inventaire/:id'             exact component    = {Inventaire}/>
                            <Route path='/procedure/:id'              exact component    = {Procedure}/>
                            <Route path='/note/:id'                   exact component    = {Note}/>
                        </Switch>
                    </div>
                </Router>
            </AuthConte.Provider  >      
        </div>
    )
}
