import React,{useState,useEffect,useContext} from 'react';
import { useHistory }   from "react-router-dom";
import'./nacre.css';
import {Liste_nacre}    from './liste_nacre'
import Pfnacre          from './pfnacre';
import deco_bar    from '../images/deco_bar_blanc.png';
import  {AuthConte}   from '../../heaauth/autfront';

export default function Nacre() {

    let history = useHistory() ;   
    
    const {ico}            = useContext(AuthConte) ; 
    const [serc,setserc]   = useState("") ;
    const [choix,setChoix] = useState(false)
    const [avy_ico,setavy_ico]                 = useState(200) ;

    var styla1 = {
        marginTop: avy_ico,
      };
      
        var styla3 = {
          marginTop: 150,
        };
      
      useEffect( ()=>{
        if(ico === true){
          setavy_ico(260)
       
        }else{
          setavy_ico(100)
        }
      
      })
    

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, []);

    const Liste_de_code_nacre = Liste_nacre.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfnacre
                        Codes={item.Codes}
                        Intitules={item.Intitules}
                        Nature={item.Nature}
                        repere={item.repere}
                    />
                </div>
            )
        } else if (item.Codes.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfnacre
                        Codes={item.Codes}
                        Intitules={item.Intitules}
                        Nature={item.Nature}
                        repere={item.repere}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfnacre
                    Codes={item.Codes}
                    Intitules={item.Intitules}
                    Nature={item.Nature}
                    repere={item.repere}
                />
            </div>
        )
    })

    const Liste_de_code_Intitules = Liste_nacre.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfnacre
                        Codes={item.Codes}
                        Intitules={item.Intitules}
                        Nature={item.Nature}
                        repere={item.repere}
                    />
                </div>
            )
        } else if (item.Intitules.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfnacre
                        Codes={item.Codes}
                        Intitules={item.Intitules}
                        Nature={item.Nature}
                        repere={item.repere}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfnacre
                    Codes={item.Codes}
                    Intitules={item.Intitules}
                    Nature={item.Nature}
                    repere={item.repere}
                />
            </div>
        )
    })

    const Btn_choix =()=>{
        setChoix(!choix)
    }
    
    const height_position          =    window.innerWidth < 650 ? styla1 : styla3
    const Nom_choix_placeholder    = choix ?   'Codes'           :   'Intitule' 
    const Nom_choix                = choix ?   'Intitule'        :   'Codes'

    const Icon_choix               = choix ?   'fas fa-align-center'       :   'fas fa-list-ol' 
    const Choix_codes_ou_intitules = choix ? Liste_de_code_nacre : Liste_de_code_Intitules

  return (
      <div className='container_nacre_parent_inscripton' style={height_position}>

          <div className='container_btn_bar'>
              <div className='container_bas_nav_nacre'>
                  <div className='titre_img_nacre'>
                      <div className='titre_nacre'>
                          <span >nacre</span>
                      </div>
                      <img
                          src={deco_bar}
                          alt="new"
                          className="deco_bar_nacre"
                      />
                  </div>
              </div>

              <div className='cherche_nacre'>
                  <div className='btn_cherche_nacre' onClick={Btn_choix}>
                      <span className='btn_text_btn_nacre'>{Nom_choix}</span>
                      <span><i className={Icon_choix}></i></span>
                  </div>
                  <div className="container_recherche">
                      <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder={Nom_choix_placeholder} onChange={(e) => { setserc(e.target.value) }} />
                      <label for="name" className="recherche_name" >
                          <span className="recontent-span" ><i className="fas fa-search"></i></span>
                      </label>
                  </div>
              </div>
        </div>

          <div className='container_bas_nav_nacre'>
              <div className='container_liste1_nacre'>
                  <span className='con_titre1_nacre_code'>Codes</span>
                  <span className='con_titre1_nacre_Intitules'>Intitules</span>
                  <span className='con_titre1_nacre'>Nature</span>
                  <span className='con_titre1_nacre_code'>Repere</span>
              </div>
              <div className='nacre_affiche'>
              {Choix_codes_ou_intitules}
          </div>
          </div>

         
      </div>
  )
  ;
}
