import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import axios          from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';

export default function Insc_fournisseur() {

    let {id} = useParams()
  /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/

    const {stateAuth} = useContext(AuthConte) ; 

    const data = {
        nom    :"",
        mail   : "",
        contact: "",
        adresse: "",
        marche : "",

    }
    
    const [info, setinfo] = useState(data)

    const handleChange = e => {
        setinfo({ ...info, [e.target.id]: e.target.value });
    }

    const Envoi_Fournisseur = () => {
        const { nom , mail, contact, adresse, marche } = info;
        axios.post('https://gadana.dev301.com/gmao_version2/fournisseurs/post_fournisseur', {
            nom          : nom ,
            mail         : mail,
            contact      : contact,
            adresse      : adresse,
            marche       : marche,
            username     : stateAuth.username ,
            UserId       : id     
        },
        {
            headers: {
              accessToken: localStorage.getItem("accessToken"),
            },
          }
        ).then((response) => {
            console.log(" bien envoyer ")
            setinfo({ ...data })
        })
    }
    const {nom , mail, contact, adresse, marche} = info;
    const btn = mail === '' || contact === '' || adresse === ''
   
    return (
        <div className='container_inc'>
            <div className='containe_second_img_fournisseur'>
                <div className='container_input_text'>
                    
                    <div className='titre_feuille'>
                        <span>Fournisseur</span>
                    </div>
                <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Nom </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='nom' maxLength={22} type="text" name="mail" id="nom"  onChange={handleChange} value={nom} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-address-card"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Mail </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='mail' maxLength={50} type="text" name="mail" id="mail"  onChange={handleChange} value={mail} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-envelope-open"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Contact </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='contact' maxLength={13} type="text" name="name" id="contact"  onChange={handleChange} value={contact}  autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="far fa-address-card"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Adresse </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='adresse' maxLength={50} type="text" name="name"  id="adresse"  onChange={handleChange} value={adresse}  autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-map-marker"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>March </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='marche' maxLength={10} type="text" name="name"  id="marche"  onChange={handleChange} value={marche}   autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-dolly"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Fournisseur}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
