import React, { useEffect, useState ,useContext }  from 'react' ;
import'./commande.css';
import { useHistory ,useParams }  from "react-router-dom";
import  {AuthConte}   from '../../heaauth/autfront';
import { Link }       from 'react-router-dom';
import Pfcommande     from './pfcommande';
import axios          from 'axios';
import deco_bar       from '../images/deco_bar_blanc.png';

export default function Commande() {

    let history = useHistory() ;
        
    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, []);
  
    let {id} = useParams() ;
    
    const {stateAuth} = useContext(AuthConte) ; 
    const {ico}       = useContext(AuthConte) ; 
    const [nom_user,setNom_user]                 = useState("")
    const [serc,setserc]                         = useState("") ;
    const [choix,setChoix]                       = useState(false) ;
    const [data_commande,setData_commande]       = useState([]);
    const [avy_ico,setavy_ico]                   = useState(200) ;

var styla1 = {
  marginTop: avy_ico,
};

  var styla3 = {
    marginTop: 120,
  };

useEffect( ()=>{
  if(ico === true){
    setavy_ico(280)
 
  }else{
    setavy_ico(50)
  }

})

    useEffect( ()=>{
        axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
            headers:{accessToken: localStorage.getItem("accessToken")},
        })
        .then( (response)=>{
            setNom_user(response.data.username)
        })
    
        axios.get(`https://gadana.dev301.com/gmao_version2/Commandes/get_commande/${id}`,{
            headers:{accessToken: localStorage.getItem("accessToken")},
        })
        .then( (response)=>{
            setData_commande(response.data)
        })
    },[])
    
    const Liste_de_code_nacre = data_commande.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfcommande
                        commande_id  = {item.id}
                        piece        = {item.piece}
                        fournisseur  = {item.fournisseur}
                        quantite     = {item.quantite}                   
                        reference    = {item.reference}
                        commentaire  = {item.commentaire}
                       />
                </div>
            )
        } else if (item.piece.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfcommande
                            commande_id  = {item.id}
                            piece        = {item.piece}
                            fournisseur  = {item.fournisseur}
                            quantite     = {item.quantite}                        
                            reference    = {item.reference}
                            commentaire  = {item.commentaire}
                          />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfcommande
                     commande_id  = {item.id}
                     piece        = {item.piece}
                     fournisseur  = {item.fournisseur}
                     quantite     = {item.quantite}                 
                     reference    = {item.reference}
                     commentaire  = {item.commentaire}
                 />
            </div>
        )
    })

    const Liste_de_code_fournisseur = data_commande.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfcommande
                             commande_id  = {item.id}
                             piece        = {item.piece}
                             fournisseur  = {item.fournisseur}
                             quantite     = {item.quantite}                   
                             reference    = {item.reference}
                             commentaire  = {item.commentaire}
                         />
                </div>
            )
        } else if (item.fournisseur.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfcommande
                              commande_id  = {item.id}
                              piece        = {item.piece}
                              fournisseur  = {item.fournisseur}
                              quantite     = {item.quantite}                          
                              reference    = {item.reference}
                              commentaire  = {item.commentaire}
                           />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfcommande
                      commande_id  = {item.id}
                      piece        = {item.piece}
                      fournisseur  = {item.fournisseur}
                      quantite     = {item.quantite}                  
                      reference    = {item.reference}
                      commentaire  = {item.commentaire}
                  />
            </div>
        )
    })

    const Btn_choix =()=>{
        setChoix(!choix)
    }

    const height_position            =  window.innerWidth < 650 ? styla1 : styla3
    const Nom_choix_placeholder      = choix ?   'Caracteristique'       :   'Fournisseur' 
    const Nom_choix                  = choix ?   'Fournisseur'           :   'Caracteristique'
    const Icon_choix                 = choix ?   'fas fa-align-center'   :   'fas fa-list-ol' 
    const Choix_piece_ou_fournisseur = choix ? Liste_de_code_nacre       : Liste_de_code_fournisseur

    const Affiche = stateAuth.username === nom_user ? Choix_piece_ou_fournisseur  :<div></div>

  return (
      <div className='container_commande_parent_inscripton'  
      style={height_position}>
       
              <div className='titre_img_commande'>
                  <div className='titre_commande'>
                      <span >Commande</span>
                  </div>
                  <img
                      src={deco_bar}
                      alt="new"
                      className="deco_bar_commande"
                  />
          </div>
          
          <div className='cherche_commande'>
              <div className='commande_chercher_container'>
                  <Link className='inventaire_choix_btn_commande' to={`/devis/${id}`} >
                      <span className='btn_devis'>Devis</span>
                      <span><i className="far fa-sticky-note"></i></span>
                  </Link>
                  <div className='btn_cherche_commande' onClick={Btn_choix}>
                      <span className='btn_text_btn_commande'>{Nom_choix}</span>
                      <span><i className={Icon_choix}></i></span>
                  </div>
              </div>

              <div className="container_recherche">
                  <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder={Nom_choix_placeholder} onChange={(e) => { setserc(e.target.value) }} />
                  <label for="name" className="recherche_name" >
                      <span className="recontent-span" ><i className="fas fa-search"></i></span>
                  </label>
              </div>
          </div>

          <div className='container_bas_nav_commande_text'>
              <div className='container_liste1_commande'>
                  <span className='con_titre1_commande_salle'>Nom</span>
                  <span className='con_titre1_commande_nom_machines'>Fournisseur</span>
                  <span className='con_titre1_commande_quantite'>Quantit</span>
                  <span className='con_titre1_commande_salle'>Reference</span>
                  <span className='con_titre1_commande_commentaire'>commentaire</span>
                  <span className='con_titre1_commande_date'>Date</span>
                  <span className='con_titre1_commande_delete'>Delete</span>
              </div>
          </div>

          <div >
              {Affiche}
          </div>
      </div>
  )
  ;
}
