import React from 'react';
import './footer.css'    ;
import Pfooter          from './pfooter';
import { useParams }    from "react-router-dom";


export default function Footer() {

    let {id}    = useParams() ;
    
       const Liste_footer = [
        {
            text :'INSCRIPTION',
            icone_accueil:'fas fa-address-book',
            path:`/inscription_accueil/${id}`,
            clas_box :'container_Pfliste_accueil1'
        },
        {
            text :'Note',
            icone_accueil:'far fa-clipboard',
            path:`/note/${id}`,
            clas_box :'container_Pfliste_accueil2'
          
        },
        {
            text :'ARCHIVES ',
            icone_accueil:'fab fa-mailchimp',
            path: `/archive/${id}`,
            clas_box :'container_Pfliste_accueil2'
        },  
    ]
    

    const Box_blox_footer =  Liste_footer.map((item, index) => {
        return (
            <Pfooter key={index}
                text           = {item.text}
                path           = {item.path}
                icone_accueil  = {item.icone_accueil}
            />
        )
    })
 
    return (
        <>
            {Box_blox_footer}
        </>
    )
}
