import React, { useEffect, useState ,useContext }  from 'react' ;
import './logine_gmao.css';
import soratra      from '../images/soratra2.png';
import Inc_logine         from '../7-inscrition_accueil/feuille_inscription/inc_logine';
import deco_bar           from '../images/deco_bar_blanc.png';
import {AuthConte}        from '../../heaauth/autfront'

export default function Logine_gmao() {


  const {ico} = useContext(AuthConte) ; 
  const [avy_ico,setavy_ico]                 = useState(200) ;

var styla1 = {
    marginTop: avy_ico,
  };
  
    var styla3 = {
      marginTop: 80,
    };
  
  useEffect( ()=>{
    if(ico === true){
      setavy_ico(200)
   
    }else{
      setavy_ico(100)
    }
  
  })


  const height_position              =   window.innerWidth < 650 ? styla1 : styla3

  return (
    <div className='container_logine_gmao'  style={height_position}>
      <div className='contine_logine_bar'>
        <div className='titre_img_logine'>
          <div className='titre_logine'>
            <span >Login</span>
          </div>
          <img
            src={deco_bar}
            alt="new"
            className="deco_bar_logine"
          />
        </div>
      </div>
      <div>
        <img
          src={soratra}
          alt="new"
          className="soratra_log"
        />
      </div>
      <Inc_logine />
    </div>
  );
}
