import React, { useEffect, useState ,useContext }  from 'react' ;
import { useHistory ,useParams }                   from "react-router-dom";
import'./inventaire.css';
import  {AuthConte}   from '../../heaauth/autfront';
import axios          from 'axios';
import Pfinveantaire  from './pfinveantaire';

export default function Inventaire() {

        let {id}    = useParams() ;
        let history = useHistory() ;


    const {ico}       = useContext(AuthConte) ; 
    const {stateAuth}                            = useContext(AuthConte) ; 
    const [nom_user,setNom_user]                 = useState("")

    const [serc,setserc]                         = useState("") ;
    const [choix,setChoix]                       = useState(false) ;
    const [data_commande,setData_commande]       = useState([]);

    const [avy_ico,setavy_ico]                 = useState(200) ;

    var styla1 = {
        marginTop: avy_ico,
      };
      
        var styla3 = {
          marginTop: 260,
        };
      
      useEffect( ()=>{
        if(ico === true){
          setavy_ico(270)
       
        }else{
          setavy_ico(100)
        }
      
      })
    
    

    useEffect(() => {
      if (!localStorage.getItem("accessToken")) {
        history.push("/");
      }
    }, []);

    useEffect( ()=>{

      axios.get(`https://gadana.dev301.com/gmao_version2/users/info_base/${id}`,{
          headers:{accessToken: localStorage.getItem("accessToken")},
      })
      .then( (response)=>{
          setNom_user(response.data.username)
      })
  
      axios.get(`https://gadana.dev301.com/gmao_version2/Inventaires/get_inventaire/${id}`,{
          headers:{accessToken: localStorage.getItem("accessToken")},
      })
      .then( (response)=>{
          setData_commande(response.data)
      })
  },[])
  
    const Liste_de_code_nacre = data_commande.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfinveantaire
                        inv_id            = {item.id}
                        piece             = {item.piece}
                        reference         = {item.reference}
                        quantite              = {item.quantite}
                        fournisseur       = {item.fournisseur}                      
                        commentaire       = {item.commentaire}
                       />
                </div>
            )
        } else if (item.piece.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfinveantaire
                           inv_id            = {item.id}
                           piece             = {item.piece}
                           reference         = {item.reference}
                           quantite              = {item.quantite}
                           fournisseur       = {item.fournisseur}                      
                           commentaire       = {item.commentaire}
                          />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfinveantaire
               inv_id            = {item.id}
               piece             = {item.piece}
               reference         = {item.reference}
               quantite              = {item.quantite}
               fournisseur       = {item.fournisseur}                      
                commentaire      = {item.commentaire}
                 />
            </div>
        )
    })

    const Liste_de_code_reference = data_commande.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfinveantaire
                         inv_id            = {item.id}
                         piece             = {item.piece}
                         reference         = {item.reference}
                         quantite              = {item.quantite}
                         fournisseur       = {item.fournisseur}                      
                          commentaire      = {item.commentaire}
                         />
                </div>
            )
        } else if (item.commentaire.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfinveantaire
                            inv_id            = {item.id}
                            piece             = {item.piece}
                            reference         = {item.reference}
                            quantite              = {item.quantite}
                            fournisseur       = {item.fournisseur}                      
                             commentaire      = {item.commentaire}
                           />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfinveantaire
                  inv_id            = {item.id}
                  piece             = {item.piece}
                  reference         = {item.reference}
                  quantite              = {item.quantite}
                  fournisseur       = {item.fournisseur}                      
                   commentaire      = {item.commentaire}
                  />               
            </div>
        )
    })

    const Btn_choix =()=>{
        setChoix(!choix)
    }

    const Nom_choix_placeholder             = choix ?   'Nom'                      : 'Caracteristique' 
    const Nom_choix                         = choix ?   'Caracteristique'          : 'Nom'
 
    const Icon_choix                        = choix ?   'fas fa-align-center'      : 'fas fa-list-ol' 
    const Choix_piece_ou_reference          = choix ? Liste_de_code_nacre          : Liste_de_code_reference
     

    const height_position              = window.innerWidth < 650 ? styla1 : styla3
   const Affiche = stateAuth.username === nom_user  ? Choix_piece_ou_reference     : <div></div>
  return (
    <div className='inventaire_container'  style={height_position}>
      <div className='inventaire_chercher_container'>
        <div>
          <div className='inventaire_choix_btn' onClick={Btn_choix}>
            <span className='inventaire_nom_btn'>{Nom_choix}</span>
            <span><i className={Icon_choix}></i></span>
          </div>
        </div>

        <div className="container_recherche">
          <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder={Nom_choix_placeholder} onChange={(e) => { setserc(e.target.value) }} />
          <label for="name" className="recherche_name" >
            <span className="recontent-span" ><i className="fas fa-search"></i></span>
          </label>
        </div>
      </div>

      <div className='inventaire_liste_nom'>
        <div className='inventaire_container_liste'>
          <span className='inventaire_nom_liste'>Nom</span>
          <span className='inventaire_quantite_liste'>Quantit</span>
          <span className='inventaire_reference_liste'>Reference</span>
          <span className='inventaire_fournisseur_liste'>Fournisseur</span>
          <span className='inventaire_commentaire_liste'>Caracteristique</span>
          <span className='inventaire_delete_liste'>Delete</span>
        </div>
      </div>

      <div >
        {Affiche}
      </div>
    </div>
  )
  ;
}

