import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import       axios   from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';

export default function Insc_Note() {
      let {id} = useParams() ;
    /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/
  
const {stateAuth} = useContext(AuthConte) ; 
  
      const data = {
          titre   :"",
          info1  : "",
          info2  : "",
          info3  : "",
      }
  
      const Envoi_Note = () => {
          const {  titre,info1, info2 , info3} = info;
          
          axios.post('https://gadana.dev301.com/gmao_version2/Notes/post_Note', {
              titre        : titre ,
              info1        : info1,
              info2        : info2,
              info3        : info3,
              username     : stateAuth.username ,
              UserId       : id ,
          },
          {
              headers: {
                accessToken: localStorage.getItem("accessToken"),
              },
            }
          ).then((response) => {
              console.log(" bien envoyer ")
              setinfo({ ...data })
          })
      }
      const [info, setinfo]                = useState(data)
      
      const handleChange = e => {
          setinfo({ ...info, [e.target.id]: e.target.value });
      }
  
      const { titre , info1, info2, info3} = info;
    
    return (
        <div className='container_inc'>
            <div className='containe_second_img_note'>
                <div className='container_input_text'>
                <div className='titre_feuille'>
                        <span>Note</span>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Titre </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='titre' maxLength={50} type="text" name="name"  id="titre"  onChange={handleChange} value={titre}  autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Info 1 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='info1' type="text" name="name"  id="info1"  onChange={handleChange} value={info1}  autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Info 2 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='info2'  type="text" name="name"  id="info2"  onChange={handleChange} value={info2} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Info 3 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='info3' type="text"  id="info3"  onChange={handleChange} value={info3} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Note}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
