
import React, {useState }  from 'react';
import axios from 'axios';
import './feuille_inscription.css';
import { Link, useHistory } from "react-router-dom";

export default function Inc_registre() {

let history = useHistory() ;

const data ={
    username  :'',
    password  :'' 
    
}
    
    const [info,setInfo]=useState(data) ;

    const handlechanchenge=(e)=>{
        setInfo({...info, [e.target.id]:e.target.value })
    }
    
    const Addregistre =()=>{
        const {username,password} = info 
      axios.post("https://gadana.dev301.com/gmao_version2/users/registre",{
            username       :username,
            password       :password
      }).then( (response)=>{
     
          setInfo({...data})
          history.push('/')
      })
      
    }
   const {username,password} = info ;
  return (
<>
          <div className='container_inc'>
              <div className='containe_second_img_registre'>
                
                  <div className='container_registre_input'>
                  <div className='titre_feuille_r'>
                      <span>Inscription</span>
                  </div>
                      <div className='container_input_text'>
                          <div className='ligne_inc'>
                              <div className='gauche_ligne'>
                                  <span>Nom </span>
                                  <span> : </span>
                              </div>
                              <div className="container_recherche_inc">
                                  <input type="text" id="username"  autoComplete="off" required type="text" className="container_input_inc" placeholder='Nom'  onChange={handlechanchenge} value={username} />
                                  <label for="name" className="recherche_name_inc" >
                                      <span className="recontent-span_inc" ><i className="fas fas fa-user-tie"></i> </span>
                                  </label>
                              </div>
                          </div>
                          <div className='ligne_inc'>
                              <div className='gauche_ligne'>
                                  <span>Mot de Passe </span>
                                  <span> : </span>
                              </div>
                              <div className="container_recherche_inc">
                                  <input type="text"  autoComplete="off" required type="text" className="container_input_inc" placeholder='Mot de passe' id="password" onChange={handlechanchenge} value={password}/>
                                  <label for="name" className="recherche_name_inc" >
                                      <span className="recontent-span_inc" > <i className="fas fa-lock"></i> </span>
                                  </label>
                              </div>
                          </div>
                      </div>
                  </div>

                  <div className='btn_envoyer_inc' onClick={Addregistre} >
                      <span className='btn_text_envoyer_inc'>Sign In</span>
                      <span><i className="far fa-paper-plane"></i></span>
                  </div>

                  <Link className='logine_pass_oublier' to='/'>
                      <span>Login</span>
                  </Link>
              </div>
          </div>
</>
  );
}
