import React,{useState,useEffect,useContext}  from 'react';
import './accueil.css';
import { useHistory, useParams }              from "react-router-dom";
import footer_fond        from '../images/footer_fond.png';
import { Accuel_Liste }   from './liste_accueil'; 
import Pfliste_accueil    from './pfliste_accueil';
import Footer             from '../1-footer/footer';
import { AuthConte}       from '../../heaauth/autfront'

export default function Accueil() {

let history                = useHistory() ;

useEffect(() => {
    if (!localStorage.getItem("accessToken")) {
      history.push("/");
    }
   }, []);
   
const {ico}                = useContext(AuthConte) ; 
let   {id}                 = useParams() ;
const [avy_ico,setavy_ico] = useState(200) ;

var styla1 = {
    marginTop: avy_ico,
  };
  
    var styla3 = {
      marginTop: 80,
    };

    useEffect( ()=>{
        if(ico === true){
          setavy_ico(310)
       
        }else{
          setavy_ico(100)
        }
      
      })
      
const height_position     =  window.innerWidth < 650 ? styla1 : styla3

    const Box_blox_milieu =  Accuel_Liste.map((item, index) => {
        return (
            <Pfliste_accueil key={index}
                text           = {item.text}
                path           = {item.path}
                icone_accueil  = {item.icone_accueil}
                clas_box       = {item.clas_box}
            />
        )
    })    

    return (
        <>
            <div>
                <div className="container_accueil_parent" style={height_position}>
                    <div className="container_accueil">
                        {Box_blox_milieu}
                    </div>
                    <div className='container_accueil_footer'>
                        <Footer id ={id}/>
                    </div>
                    <img
                        src={footer_fond}
                        alt="new"
                        className="footer_image_fond_accueils"
                    />
                </div>
            </div>
        </>
    )

}


