
import React, { useEffect, useState ,useContext }  from 'react' ;
import './registre_gmao.css';
import Inc_registre from '../7-inscrition_accueil/feuille_inscription/inc_registre';
import deco_bar     from '../images/deco_bar_blanc.png';
import soratra      from '../images/soratra2.png';
import {AuthConte}  from '../../heaauth/autfront'

export default function Registre_gmao() {

    const {ico} = useContext(AuthConte) ; 
    const [avy_ico,setavy_ico]                 = useState(200) ;
  
  var styla1 = {
      marginTop: avy_ico,
    };
    
      var styla3 = {
        marginTop: 80,
      };
    
    useEffect( ()=>{
      if(ico === true){
        setavy_ico(180)
     
      }else{
        setavy_ico(5)
      }
    
    })
  
  

    const height_position              =   window.innerWidth < 650 ? styla1 : styla3

  return (
      <div className='container_registre_gmao'  style={height_position}>
          <div className='contine_registre_bar'>
              <div className='titre_img_registre'>
                  <div className='titre_registre'>
                      <span >Inscription</span>
                  </div>
                  <img
                      src={deco_bar}
                      alt="new"
                      className="deco_bar_registre"
                  />
              </div>
          </div>
          <div>
                   <img
                        src={soratra}
                        alt="new"
                        className="soratra"
                    />
                   </div>
          <Inc_registre />
      </div>
  );
}
