
import React from 'react';
import axios          from 'axios';

export default function Pfdevis({
    dev_id        = '',
    piece         ='',
    quantite      ='',
    reference     ='',
    commentaire   ='',
}) {

    
const deletePost = (dev_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/Devis/del_devi/${dev_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime")
       window.location.reload();
      });

     
  };
  return (
    <>      
     <div   >
              <div className='container_liste1_commande2'>
                  <div className='con_titre1_commande2_nom'>
                      <span >{piece}</span>
                  </div>
                  <div className='con_titre1_commande2_quantite'>
                      <div className='cmd2_fournissseur'>
                          <span >{quantite}</span>
                      </div>
                  </div>

                  <div className='con_titre1_commande2_nom' >
                      <span >{reference}</span>
                  </div>
                      <div className='con_titre1_commande2_commentaire_pour_devis'>
                          <p >{commentaire}</p>
                  </div>
                  <span className='con_titre1_commande2_date'>23/01/2022</span>
                  <div className='con_titre1_commande2_delete' onClick={() => deletePost(dev_id)}>
                      <span> <i className="fas fa-trash"></i></span>
                  </div>
              </div>
          </div>
</>
  );
}

