
    import React, { useState } from 'react';
    import'./procedure';
    import procedure_titre from '../images/procedure_fond_titre.png';
    import axios                from 'axios';
    
    export default function Pfprocedure({
    proc_id ='',
    titre   = '',  
    etape1  = '',
    etape2  = '',
    etape3  = '',
    etape4  = '',
    etape5  =  '',
    etape6  = '',
    etape7  = '',
    etape8  = '',
    etape9  = '',
    etape10 = '',
    etape11 = '',
    etape12 = '',


    }) {

    const [affiche,setAffiche]=useState(false);

    const Click_affiche =()=>{
        setAffiche(!affiche)

    }

    const deletePost = (proc_id) => {
        axios
          .delete(`https://gadana.dev301.com/gmao_version2/Procedures/del_procedure/${proc_id}`, {
            headers: { accessToken: localStorage.getItem("accessToken") },
          })
          .then(() => {
           console.log("bien suprime");
              window.location.reload();
          });
      };
      
    
    const Quand_affiche = affiche ? 
        <div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span className='etape_span'>{etape1}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span'>{etape2}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span'>{etape3}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span'>{etape4}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span'>{etape5}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span'>{etape6}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape7}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape8}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape9}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape10}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape11}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span  className='etape_span' >{etape12}</span>
            </div>
        </div>
        :
        <div></div>

        const Affice_delete = affiche ? <div></div> :<div className="procedure_delete" ><span onClick={()=>deletePost(proc_id)} className='procedure_enfant_delete'><i className="fas fa-trash"></i></span></div>

      return (
          <div className='procedure_pf_container'>
              <div className='procedure_con_titre1_nacre2_Intitules_titre' onClick={Click_affiche} >
                  <img
                      src={procedure_titre}
                      alt="new"
                      className="procedure_image_fond"
                  />
                  <span  className='etape_span_titre' >{titre}</span>
              </div>
              {Quand_affiche}
              {Affice_delete}
          </div>

      );
    }