
import React ,{useState,useEffect,useContext}from 'react';
import'../5-commande/commande.css';
import { useHistory }         from "react-router-dom";
import  {AuthConte}           from '../../heaauth/autfront';
import deco_bar               from '../images/deco_bar_blanc.png';
import Insc_devis             from '../7-inscrition_accueil/feuille_inscription/insc_devis';
import Porteur_imprimer       from './pdf_impression/porteur_imprimer';
import Devis_feuille_imprimer from './pdf_impression/devis_feuille_imprimer';



export default function Devis() {

    let history = useHistory() ;
    const {ico} = useContext(AuthConte) ; 

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, []);

      const [avy_ico,setavy_ico]                 = useState(200) ;

      var styla1 = {
          marginTop: avy_ico,
        };
        
          var styla3 = {
            marginTop: 80,
          };
        
        useEffect( ()=>{
          if(ico === true){
            setavy_ico(250)
         
          }else{
            setavy_ico(50)
          }
        
        })
      
  const height_position              =    window.innerWidth < 650 ? styla1 : styla3
    
  return (
      <div className='container_devis_parent_inscripton' style={height_position}>
            
            <div className='titre_img_commande'>
                  <div className='titre_commande'>
                      <span >Devis</span>
                  </div>
                  <img
                      src={deco_bar}
                      alt="new"
                      className="deco_bar_commande"
                  />
          </div>

          <div>
              <Insc_devis />
          </div>
          <div className='pa_devis_feuil'>
              <Devis_feuille_imprimer />
          </div>
          <Porteur_imprimer />
      </div>
  )
  ;
}
