
import React,{useEffect, useState,useContext} from 'react';
import { useHistory  }     from "react-router-dom";
import'./dans_le_marche.css';
import axios            from 'axios';
import Pfdans_le_marche from './pfdans_le_marche';
import deco_bar         from '../images/deco_bar_blanc.png';
import { AuthConte}       from '../../heaauth/autfront'

export default function Dans_le_marche() {
 

    let history = useHistory() ;

    const [serc,setserc]                         = useState("") ;
    const [choix,setChoix]                       = useState(false) ;
    const [data_dans_le_marche,setData_dans_le_marche] = useState([]);
    const {ico}       = useContext(AuthConte) ; 
    const [avy_ico,setavy_ico]               =useState(200) ;

var styla1 = {
  marginTop: avy_ico,
};

  var styla3 = {
    marginTop: 120,
  };

useEffect( ()=>{
  if(ico === true){
    setavy_ico(315)
 
  }else{
    setavy_ico(100)
  }

})

    useEffect( ()=>{
        axios.get("https://gadana.dev301.com/arduino2/get_maintefoursmarch").then( (response)=>{
            setData_dans_le_marche(response.data)
        })
    },[])

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
          history.push("/");
        }
      }, []);

    
    const Liste_de_code_nacre = data_dans_le_marche.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfdans_le_marche
                        referencessifac = {item.referencessifac}
                        fournisseur     = {item.fournisseur}
                        numerocontrat   = {item.numerocontrat}
                        numerodeposte   = {item.numerodeposte}
                    />
                </div>
            )
        } else if (item.numerodeposte.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfdans_le_marche
                           referencessifac = {item.referencessifac}
                           fournisseur  = {item.fournisseur}
                           numerocontrat        = {item.numerocontrat}
                           numerodeposte   = {item.numerodeposte}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfdans_le_marche
                   referencessifac = {item.referencessifac}
                   fournisseur  = {item.fournisseur}
                   numerocontrat        = {item.numerocontrat}
                   numerodeposte   = {item.numerodeposte}
                />
            </div>
        )
    })

    const Liste_de_code_fournisseur = data_dans_le_marche.filter((item, index) => {
        if (serc === "") {
            return (
                <div key={index}>
                    <Pfdans_le_marche
                          referencessifac = {item.referencessifac}
                          fournisseur  = {item.fournisseur}
                          numerocontrat        = {item.numerocontrat}
                          numerodeposte   = {item.numerodeposte}
                    />
                </div>
            )
        } else if (item.fournisseur.toLocaleLowerCase().includes(serc.toLocaleLowerCase())) {
            return (
                <div key={index}>
                    <Pfdans_le_marche
                            referencessifac = {item.referencessifac}
                            fournisseur  = {item.fournisseur}
                            numerocontrat        = {item.numerocontrat}
                            numerodeposte   = {item.numerodeposte}
                    />
                </div>
            )
        }
    }).map((item, index) => {
        return (
            <div key={index}>
                <Pfdans_le_marche
                   referencessifac = {item.referencessifac}
                   fournisseur  = {item.fournisseur}
                   numerocontrat        = {item.numerocontrat}
                   numerodeposte   = {item.numerodeposte}
                />
            </div>
        )
    })


    const Btn_choix =()=>{
        setChoix(!choix)
    }
    
    const height_position     =  window.innerWidth < 650 ? styla1 : styla3

    const Nom_choix_placeholder    = choix ?   'Nacre'                  :   'Fournisseur' 
    const Nom_choix                = choix ?   'Fournisseur'            :   'Nacre'

    const Icon_choix               = choix ?   'fas fa-align-center'    :   'fas fa-list-ol' 
    const Choix_referencessifac_ou_fournisseur = choix ? Liste_de_code_nacre : Liste_de_code_fournisseur

  return (
      <div className='container_dans_le_marche_parent_inscripton' style={height_position}>
          <div className='container_bas_march'>
              <div className='titre_img_fournisseur_marche'>
                  <div className='titre_dans_le'>
                      <span >March</span>
                  </div>
                  <img
                      src={deco_bar}
                      alt="new"
                      className="deco_bar_contact"
                  />
              </div>
          </div>

          <div className='cherche_dans_le_marche'>
              <div className='btn_cherche_dans_le_marche' onClick={Btn_choix}>
                  <span className='btn_text_btn_dans_le_marche'>{Nom_choix}</span>
                  <span><i className={Icon_choix}></i></span>
              </div>
              <div className="container_recherche">
                  <input type="text" name="name" autoComplete="off" required type="text" className="container_input" placeholder={Nom_choix_placeholder} onChange={(e) => { setserc(e.target.value) }} />
                  <label for="name" className="recherche_name" >
                      <span className="recontent-span" ><i className="fas fa-search"></i></span>
                  </label>
              </div>
          </div>

          <div className='container_bas_nav_dans_le_marche'>
              <div className='container_liste1_dans_le_marche'>
                  <span className='con_titre1_dans_le_marche_referencessifac'>Sifac</span>
                  <span className='con_titre1_dans_le_marche_fournisseur'>Fournisseur</span>
                  <span className='con_titre1_dans_le_marche'>Numero Contrat</span>
                  <span className='con_titre1_dans_le_marche_numerodeposte'>Nacre</span>
              </div>
          </div>

          <div >
              {Choix_referencessifac_ou_fournisseur}
          </div>
      </div>
  )
  ;
}
