import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import       axios   from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';

export default function Insc_archive() {

    let {id} = useParams() ;
    /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/
  
const {stateAuth} = useContext(AuthConte) ; 
  
      const data = {
          lien1  : "",
          lien2  : "",
          lien3  : "",
          lien4  : "",
          lien5  :'',
          lien6  :''
      }
  
      const Envoi_Archive = () => {
          const {  lien1, lien2 , lien3, lien4 ,lien5 ,lien6} = info;
          axios.post('https://gadana.dev301.com/gmao_version2/archives/post_archive', {
              lien1        : lien1,
              lien2        : lien2,
              lien3        : lien3,
              lien4        : lien4,
              lien5        : lien5 ,
              lien6        : lien6 ,
              username     : stateAuth.username ,
              UserId       : id ,
          },
          {
              headers: {
                accessToken: localStorage.getItem("accessToken"),
              },
            }
          ).then((response) => {
              console.log(" bien envoyer ")
              setinfo({ ...data })
          })
      }
      const [info, setinfo]                = useState(data)
      
      const handleChange = e => {
          setinfo({ ...info, [e.target.id]: e.target.value });
      }
  
      const { lien1, lien2, lien3, lien4,lien5,lien6} = info;
      
    return (
        <div className='container_inc'>
            <div className='containe_second_img_devis'>
                <div className='container_input_text'>
                <div className='titre_feuille'>
                        <span>Archive</span>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien1 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien1'  type="text" name="name"  id="lien1"  onChange={handleChange} value={lien1} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien2</span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien2' type="text" name="name" id="lien2"  onChange={handleChange} value={lien2} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien3 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien3' type="text" name="name" id="lien3"  onChange={handleChange} value={lien3} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>

                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien4 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien4' type="text" name="name" id="lien4"  onChange={handleChange} value={lien4} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien5</span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien5' type="text" name="name" id="lien5"  onChange={handleChange} value={lien5} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" >  <i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Lien6 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='lien6'type="text" name="name" id="lien6"  onChange={handleChange} value={lien6} autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-up"></i></span>
                            </label>
                        </div>
                    </div>
                </div>

                <div className='btn_envoyer_inc' onClick={Envoi_Archive}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
