import React from 'react';
import'./maintenance.css';
import axios          from 'axios';

export default function Pfmaintenance({
    maint_id    ='',
    salle       ='',
    materiel    ='',
    panne       ='',
    commentaire ='',
}) {

    

const deletePost = (maint_id) => {
    axios
      .delete(`https://gadana.dev301.com/gmao_version2/Maintenances/del_maintenance/${maint_id}`, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then(() => {
       console.log("bien suprime")
       window.location.reload();
      });
  };

  return (
      <div>
          <div className='container_bas_nav_maintenance2'>
              <div className='container_liste1_maintenance2'>
                  <span className='con_titre1_maintenance2_salle'>{salle}</span>
                  <span className='con_titre1_maintenance2_machine'>{materiel}</span>
                  <span className='con_titre1_maintenance2'>{panne}</span>
                  <span className='con_titre1_maintenance2_commentaire'>{commentaire}</span>
                  <span className='con_titre1_maintenance2_date' >23/01/2022</span>

                  <div className='con_titre1_maintenance2_delete' onClick={()=>deletePost(maint_id)}>
                  <span> <i className="fas fa-trash"></i></span>
                  </div>
                
              </div>
          </div>
      </div>
  );
}


