import React, { useRef} from 'react';
import ReactToPrint from 'react-to-print';
import Impression   from './impression';


export default function Porteur_imprimer() {

    const componentRef = useRef();

    return (
      <>
        <div className="porteur_imprimer_bloc" >
          <Impression ref={componentRef} />
        </div>
        <div >
          <ReactToPrint
            trigger={() =>
              <div className='inventaire_choix_btn_devis'>
                <span className='inventaire_nom_btn_devis'>Impression</span>
                <span><i className="far fa-file-pdf"></i></span>
              </div>}
            content={() => componentRef.current}
          />
        </div >
      </>
    )

}
