import React ,{useState}   from 'react';
import procedure_titre     from '../images/procedure_fond_titre.png';
import axios               from 'axios';

export default function Pfnote({
    note_id ='',
    titre = '',  
    info1 = '',
    info2 = '',
    info3 = '',

}) {

    const [affiche,setAffiche]=useState(false)
    const Click_affiche =()=>{
        setAffiche(!affiche)
    }
    
    const deletePost = (note_id) => {
        axios
          .delete(`https://gadana.dev301.com/gmao_version2/Notes/del_note/${note_id}`, {
            headers: { accessToken: localStorage.getItem("accessToken") },
          })
          .then(() => {
           console.log("bien suprime");
           window.location.reload();
          });
      };
    const Quand_affiche = affiche ? 
        <div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span className='etape_span'>{titre}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span className='etape_span'>{info1}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span className='etape_span'>{info2}</span>
            </div>
            <div className='procedure_con_titre1_nacre2_Intitules'>
                <span className='etape_span'>{info3}</span>
            </div>
        </div>

        :
        <div></div>

        const Affice_delete = affiche ? <div></div> :<div className="procedure_delete" ><span onClick={()=>deletePost(note_id)} className='procedure_enfant_delete'><i className="fas fa-trash"></i></span></div>
  return (
    <div className='procedure_pf_container'>
    <div className='procedure_con_titre1_nacre2_Intitules_titre' onClick={Click_affiche} >
        <img
            src={procedure_titre}
            alt="new"
            className="procedure_image_fond"
        />
        <span className='etape_span_titre'>{titre}</span>
    </div>
    {Quand_affiche}
    {Affice_delete}
</div>
  );
}
