import React,{useState,useContext} from 'react';
import './feuille_inscription.css';
import       axios   from 'axios';
import { AuthConte } from '../../../heaauth/autfront';
import { useParams } from 'react-router-dom';


export default function Insc_procedure() {



    let {id} = useParams() ;
    /*/ inc_fournisseur ne pas dans path mais id => dans inscription_accueil  le inc_ consomme la meme chose/*/
  
const {stateAuth} = useContext(AuthConte) ; 
  
      const data = {
          titre   :"",
          etape1  : "",
          etape2  : "",
          etape3  : "",
          etape4  : "",
          etape5  :'',
          etape6  :''
      }
  
      const Envoi_Procedure = () => {
          const {  titre,etape1, etape2 , etape3, etape4 ,etape5 ,etape6} = info;
          axios.post('https://gadana.dev301.com/gmao_version2/Procedures/post_procedure', {
              titre         : titre ,
              etape1        : etape1,
              etape2        : etape2,
              etape3        : etape3,
              etape4        : etape4,
              etape5        : etape5 ,
              etape6        : etape6 ,
              username     : stateAuth.username ,
              UserId       : id ,
          },
          {
              headers: {
                accessToken: localStorage.getItem("accessToken"),
              },
            }
          ).then((response) => {
              console.log(" bien envoyer ")
              setinfo({ ...data })
          })
      }
      const [info, setinfo]                = useState(data)
      
      const handleChange = e => {
          setinfo({ ...info, [e.target.id]: e.target.value });
      }
  
      const { titre , etape1, etape2, etape3, etape4,etape5,etape6} = info;
      
    return (
        <div className='container_inc'>
            <div className='containe_second_img_procedure'>
                <div className='container_input_text'>
                <div className='titre_feuille'>
                        <span>Procedure</span>
                    </div>
                <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Titre </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='titre'  type="text" id="titre"  onChange={handleChange} value={titre} name="name"   autoComplete="off" required type="text" className="container_input_inc"  maxlength={50}/>
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-text-width"></i></span>
                                
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 1 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape1'  type="text"  id="etape1"  onChange={handleChange} value={etape1} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 2 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape2'  type="text"  id="etape2"  onChange={handleChange} value={etape2} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>

                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 3 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape3'  type="text"  id="etape3"  onChange={handleChange} value={etape3} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 4 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape4'  type="text"  id="etape4"  onChange={handleChange} value={etape4} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 5 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape5'  type="text"  id="etape5"  onChange={handleChange} value={etape5} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className='ligne_inc'>
                        <div className='gauche_ligne'>
                            <span>Etape 6 </span>
                            <span> : </span>
                        </div>
                        <div className="container_recherche_inc">
                            <input placeholder='etape6'  type="text" id="etape6"  onChange={handleChange} value={etape6} name="name" autoComplete="off" required type="text" className="container_input_inc" />
                            <label for="name" className="recherche_name_inc" >
                                <span className="recontent-span_inc" ><i className="fas fa-sort-numeric-down-alt"></i></span>
                            </label>
                        </div>
                    </div>
                </div>
                <div className='btn_envoyer_inc' onClick={Envoi_Procedure}>
                    <span className='btn_text_envoyer_inc'>Envoyer</span>
                    <span><i className="far fa-paper-plane"></i></span>
                </div>
            </div>
        </div>
    )
}
